import api from "../../../../../../constants/api";
import Service from "../../../../../../services/request";

export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_USER.method,
      path: api.LIST_USER.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_USER",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};

export const getUser = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_USER.method,
      path: api.GET_USER.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_USER",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};

export const addUser = (User) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_USER.method,
      path: api.CREATE_USER.path,
      data: User,
    })
      .then((response) => {
        dispatch({
          type: "ADD_USER",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_USER",
          err,
        })
      );
  };
};
export const updateUser = (User) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_USER.method,
      path: api.UPDATE_USER.path,
      data: User,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_USER",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_USER",
          err,
        })
      );
  };
};


